.filter__label {
  display: flex;
  align-items: center;

  margin: 0;
  padding: 0;

  cursor: pointer;
}

/*   label  before    */
.filter__label > span::before {
  content: "";

  border: 0.7px solid #9f9e9e;
  box-sizing: border-box;
  border-radius: 2px;

  width: 24px;
  height: 24px;

  margin: 0;
  padding: 0;
  margin-right: 16px;
}

/*      checkbox */
.filter__label > input:not(:disabled):not(:checked) + span:hover::before {
  border: 0.7px solid #585757;
}

/*     (   ) */
.filter__label > input:not(:disabled):active + span::before {
  border: 0.7px solid #c1c1c1;
}

/*   ,    */
.filter__label > input:focus + span::before {
  border: 0.7px solid #585757;
}

/*   ,    checked */
.filter__label > input:checked + span::before {
  background-image: url("../../img/icon-check.svg");
  background-repeat: no-repeat;
  background-position: center;
}

/*   ,    disabled */
.filter__label > input:disabled + span::before {
  background: #f7f3f3;
  border: 0.7px solid #dddddd;
}

.filter__label > input:disabled + span {
  color: #dddddd;
}
